/* media breakpoint mixin */
.link, .admin-btn, .lang__list a, .lang-content a,
.lang-dropdown__list a,
.lang-dropdown-content a, .breadcrumbs a {
  position: relative;
  display: inline-block;
  cursor: pointer;
  transition-timing-function: ease-in-out;
  transition-duration: 300ms;
  transition-property: all;
}

.link:after, .admin-btn:after, .lang__list a:after, .lang-content a:after,
.lang-dropdown__list a:after,
.lang-dropdown-content a:after, .breadcrumbs a:after {
  content: '';
  position: absolute;
  background: currentColor;
  height: 1px;
  bottom: 1px;
  left: 0px;
  width: 100%;
  transition: inherit;
  transform: scaleX(0);
}

@media (any-hover: hover) {
  .link:hover:after, .admin-btn:hover:after, .lang__list a:hover:after, .lang-content a:hover:after,
  .lang-dropdown__list a:hover:after,
  .lang-dropdown-content a:hover:after, .breadcrumbs a:hover:after {
    -webkit-transform: scaleX(1);
    -moz-transform: scaleX(1);
    -ms-transform: scaleX(1);
    -o-transform: scaleX(1);
    transform: scaleX(1);
  }
}

.is-close {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.is-open {
  opacity: 1;
  pointer-events: auto;
  visibility: visible;
}

