.header__burger.is-active .header__burger-wrapper:before {
  top: calc(50% - 2px);
  transform: rotate(45deg);
}

.header__burger.is-active .header__burger-wrapper:after {
  bottom: 50%;
  transform: rotate(-45deg);
}

.header__burger-wrapper {
  position: relative;
  width: 20px;
  height: 14px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.header__burger-wrapper:before, .header__burger-wrapper:after,
.header__burger-wrapper span {
  width: 100%;
  height: 2px;
  background: currentColor;
  transition-property: all;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
}

.header__burger-wrapper:before, .header__burger-wrapper:after {
  content: '';
  position: absolute;
  left: 0px;
}

.header__burger-wrapper:before {
  top: 0px;
}

.header__burger-wrapper:after {
  bottom: 0px;
}

.header__modal {
  position: absolute;
  z-index: 1;
  width: 100%;
  min-height: calc(100 * var(--vh));
  top: 0px;
  left: 0px;
  background: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
  transform: scale(0);
  transform-origin: right top;
  transition-property: all;
  transition-duration: 300ms;
  transition-timing-function: ease-in-out;
}

.header__modal.is-open {
  transform: scale(1);
}

@media only screen and (min-width: 1200px) {
  .header__modal {
    display: none;
  }
}

.header__modal .menu {
  align-items: center;
}

.header__modal .menu a,
.header__modal .lang {
  font-size: 24px;
}

